@font-face {
  font-family: "Cormorant Garamond";
  src: url("../../assets/fonts/Cormorant_Garamond/CormorantGaramond-Italic.woff2")
      format("woff2"),
    url("../../assets/fonts/Cormorant_Garamond/CormorantGaramond-Italic.woff")
      format("woff"),
    url("../../assets/fonts/Cormorant_Garamond/CormorantGaramond-Italic.ttf")
      format("truetype");
  font-weight: normal;
  font-style: italic;
  font-display: swap;
}

